<template lang="pug">
    div.phone-register-conten
        div.edit-photo
            div.add-photo
                vue-core-image-upload.upload-img(
                    :class="['btn', 'btn-primary']"
                    @imageuploading="imageuploading"
                    @imageuploaded="imageuploaded"
                    :max-file-size="5242880"
                    :url="doUploadUrl"
                    text=""
                    inputOfFile="head_image"
                    extensions="png,jpg,gif"
                )
                    img(v-if="head_image" v-lazy="head_image")
                    img(v-else src="../../assets/img/no-avatar.png")
            p.prompt-text 
        div.edit-name
            input.nickname(v-model="nickname" maxlength="16")
            div.limit-input 
                span(v-text="currentLength")
                span /16
        div.edit-sex
            i.icon.iconfont(:class="{'activeMan' : sex == 1}" @click="manActive") &#xe661;
            span 
            i.icon.iconfont(:class="{'activeWoman' : sex == 2}" @click="womanActive") &#xe662;
        div.btn-group
            div.btn-confirm(@click="submit") 
</template>
<script>
import queryString from 'querystring'
import VueCoreImageUpload from 'vue-core-image-upload'
import { Toast, Indicator } from 'mint-ui'
import { mapActions } from 'vuex'
export default {
    name: 'loginRegister',
    components: {
        'vue-core-image-upload': VueCoreImageUpload,
    },
    data() {
        return {
            head_image: '',
            sex: 1,
            nickname: '',
            currentLength: '',
        }
    },
    created() {
        this.nickname = this.$route.query.user_id;
    },

    methods: {
        ...mapActions(['setUserInfo']),
        //- -
        imageuploading() {
            Indicator.open('...')
        },
        imageuploaded(result) {
            Indicator.close()
            if (result.state == 200) {
                this.head_image = result.fullname;
            }
            else {
                Toast(result.message || '');
            }
        },
        //- ---
        manActive() {
            this.sex = 1;
        },
        //- ---
        womanActive() {
            this.sex = 2;
        },
        check() {
            if (this.currentLength > 16) {
                Toast('');
                return false;
            }
            else if (!$.trim(this.nickname)) {
                Toast('');
                return false;
            }
            else {
                return true;
            }
        },
        submit() {
            if (this.check()) {
                Indicator.open();
                let axiosUrl = this.api.loginDoUpdate();
                this.axios.post(axiosUrl, queryString.stringify({
                    nick_name: this.nickname,
                    oss_path: this.head_image,
                    sex: this.sex,
                })).then(res => {
                    Indicator.close();
                    if (res.data.status == 1) {
                        this.axios.get(this.api.getUserHome()).then(res => {
                            this.setUserInfo(res.data.user);
                            let instance = Toast('');

                            setTimeout(() => {
                                instance.close();
                                this.$router.push({
                                    path: '/',
                                });
                            }, 1000);
                        });
                    }
                    else {
                        Toast(res.data.error || '');
                        Indicator.close();
                    }
                })
                    .catch(err => {
                        Toast(err || '');
                        Indicator.close();
                    })
            }

        }
    },
    computed: {
        doUploadUrl() {
            return this.api.doUpload();
        }
    },
    watch: {
        nickname() {
            this.currentLength = $.trim(this.nickname).length;
        }
    }
}




</script>

<style lang="less" scoped>
@import '../../assets/css/variable.less';
.phone-register-conten {
    .edit-photo {
        padding: .9rem 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        .add-photo {
            height: 3rem;
            width: 3rem;
            border-radius: 2.5rem;
            margin-top: 1rem;
            border: 2px solid #fff;
            .upload-img {
                height: 100%;
                width: 100%;
                border-radius: 2.5rem;
            }
            img {
                height: 100%;
                width: 100%;
            }
        }
        .prompt-text {
            margin-top: .6rem;
            color: #9f9f9f;
            font-size: .75rem;
        }
    }
    .edit-name {

        input {
            width: 100%;
            border: none;
            outline: none;
            height: 2rem;
            padding: 0 .5rem;
        }
        .limit-input {
            margin-top: .65rem;
            text-align: right;
            font-size: .75rem;
            color: @fc-light;
            margin-right: 1.25rem;
        }
    }
    .edit-sex {
        display: flex;
        justify-content: center;
        align-items: center;
        .iconfont {
            font-size: 2.5rem;
            color: #d7d7d7;
        }
        .activeMan {
            color: @color-blue;
        }
        .activeWoman {
            color: @color-red;
        }
        span {
            color: @fc-light;
            font-size: .8rem;
            padding: 0 .5rem;
        }
    }
    .btn-group {
        margin-top: 1rem;
        padding: 0 .5rem;
        .btn-confirm {
            height: 2rem;
            text-align: center;
            line-height: 2rem;
            background-color: @color-theme;
            color: #fff;
            border-radius: 1rem;
        }
    }
}
</style>



